import http from '@/http/interface.js'
import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
export default {
	components: {cmdNavBar},
	data() {
		return {
			oldPass : '',
			newPass1 : '',
			newPass2 : ''
		};
	},
	onLoad() {
		
	},
	methods:{
		goChange(){
			if(this.oldPass == '' || this.newPass1 == '' || this.newPass2 == ''){
				uni.showToast({
					title: '不能输入空值',
					position: 'bottom',
					icon: 'none',
					duration: 2500
				})
				return;
			}
			if(this.newPass1 != this.newPass2){
				uni.showToast({
					title: '两次输入密码不一致',
					position: 'bottom',
					icon: 'none',
					duration: 2500
				})
				return;
			}
			
			http.get('member/updatePassword',{phone:this.$store.state.userInfo.phone,loginPwd:this.oldPass,newLoginPwd:this.newPass1}).then((res)=>{
				this.$store.commit('mainPageNumUpdate',1);
				this.$store.commit('userGoOut',{});
				uni.reLaunch({
					url : '/pages/checkpoint/login/login?msg=修改密码成功，请您重新登录'
				})
			})
			
		}
	}
}
